@import (less) './animate.css';

@plone-grid-gutter-width: 30px;
@plone-screen-xs-min:              480px;
@plone-screen-sm-min:              768px;
@plone-screen-md-min:              992px;
@plone-screen-lg-min:              1200px;
@plone-container-sm: ((720px + @plone-grid-gutter-width));
@plone-container-md: ((940px + @plone-grid-gutter-width));
@plone-container-lg: ((1140px + @plone-grid-gutter-width));
@plone-left-toolbar-expanded: 120px;
@plone-left-toolbar: 60px;
@white: #fff;
@black: #000;

body#visual-portal-wrapper.pat-plone #edit-zone .contenttype-sliderimage:after {
  content: '\e810';
}

.carousel, #content .carousel, #content #content-core .carousel {
  .item {
    height: auto;
    .text-wrap {
      height: 800px;
      display: table;
      width: 100%;
      background: -webkit-gradient(radial, ellipse at center, rgba(0, 0, 0, 0.25) 0%, rgba(0, 0, 0, 0.0) 75%);
      background: -webkit-radial-gradient(ellipse at center, rgba(0, 0, 0, 0.25) 0%, rgba(0, 0, 0, 0.0) 75%);
      background: -moz-radial-gradient(ellipse at center, rgba(0, 0, 0, 0.25) 0%, rgba(0, 0, 0, 0.0) 75%);
      background: -o-radial-gradient(ellipse at center, rgba(0, 0, 0, 0.25) 0%, rgba(0, 0, 0, 0.0) 75%);
      background: radial-gradient(ellipse at center, rgba(0, 0, 0, 0.25) 0%, rgba(0, 0, 0, 0.0) 75%);
      .slide-text {
        display: table-cell;
        vertical-align: middle;
        z-index: 10;
        position: relative;
        text-shadow: rgba(0, 0, 0, 0, 0.5) 1px 1px 1px;
        a {
          color: @white;
          border-bottom: 1px solid rgba(255, 255, 255, 0.5);
          &:hover {
          text-decoration: none;
          color: @white;
          border-bottom: 1px solid @white;
          }
        }
        .slide-lead, .item .slide-sub-lead {
          color: #fff;
          margin: 0;
          padding: 0;
        }
        .slide-lead {
          font-weight: 500;
          font-size: 2.75rem;
          margin-bottom: 0;
        }
        .slide-sub-lead {
          font-weight: 400;
          font-size: 2.5rem;
          line-height: 42px;
          margin-top: 30px;
        }
      }
    }
    .carousel-caption {
      pointer-events: none;
      a {
        pointer-events: auto;
      }
      h3.slide-leade, h4.slide-sub-lead {
        color: @white;
      }
      padding: 0;
      bottom: auto;
      top: 50%;
      transform: translateY(-50%);
    }
  }
  ol.carousel-indicators {
    list-style-type: none;
    margin: 0;
    max-width: 60%;
    width: auto;
    li {
      margin-bottom: 0;
      line-height: initial;
      display: inline-block;
      border: 2px solid @black;
      text-shadow: 0 1px 2px rgba(255, 255, 255, 0.6);
    }
  }
  .carousel-control {
    .icon-prev:before, .icon-next:before {
      color: @white;
    }
  }
  span.banner-img {
    display: block;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    width: 100%;
  }
}

.to-animate {
  opacity: 0;
}

.background-image-tile {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;

  &.parallax {
    background-attachment: fixed;
  }

  .colored-background {
    padding: 65px 0;
  }
}

.mosaic-tile-light-text {
  color: white;
  p, h2, h3, h4, h5, h6 {
    color: white;
  }
}

.mosaic-tile.mosaic-tile-full-width {
  margin-left: -@plone-grid-gutter-width/2;
  margin-right: -@plone-grid-gutter-width/2;
  @media(min-width: @plone-screen-sm-min) {
    margin-left: calc(~"-100vw / 2 +" (@plone-container-sm - @plone-grid-gutter-width) ~"/ 2");
    margin-right: calc(~"-100vw / 2 +" (@plone-container-sm - @plone-grid-gutter-width) ~"/ 2");  
  }
  @media(min-width: @plone-screen-md-min) {
    margin-left: calc(~"-100vw / 2 +" (@plone-container-md - @plone-grid-gutter-width) ~"/ 2");
    margin-right: calc(~"-100vw / 2 +" (@plone-container-md - @plone-grid-gutter-width) ~"/ 2");
  }
  @media(min-width: @plone-screen-lg-min) {
    margin-left: calc(~"-100vw / 2 +" (@plone-container-lg - @plone-grid-gutter-width) ~"/ 2");
    margin-right: calc(~"-100vw / 2 +" (@plone-container-lg - @plone-grid-gutter-width) ~"/ 2");
  }
  .plone-toolbar-left-default & {
    @media(min-width: (@plone-screen-sm-min + @plone-left-toolbar)) {
      margin-left: calc(~"-100vw / 2 +" (@plone-container-sm  - @plone-grid-gutter-width + @plone-left-toolbar) ~"/ 2");
      margin-right: calc(~"-100vw / 2 +" (@plone-container-sm - @plone-grid-gutter-width + @plone-left-toolbar) ~"/ 2");
    }
    @media(min-width: (@plone-screen-md-min + @plone-left-toolbar)) {
      margin-left: calc(~"-100vw / 2 +" (@plone-container-md - @plone-grid-gutter-width + @plone-left-toolbar) ~"/ 2");
      margin-right: calc(~"-100vw / 2 +" (@plone-container-md - @plone-grid-gutter-width + @plone-left-toolbar) ~"/ 2");
    }
    @media(min-width: (@plone-screen-lg-min + @plone-left-toolbar)) {
      margin-left: calc(~"-100vw / 2 +" (@plone-container-lg - @plone-grid-gutter-width + @plone-left-toolbar) ~"/ 2");
      margin-right: calc(~"-100vw / 2 +" (@plone-container-lg - @plone-grid-gutter-width + @plone-left-toolbar) ~"/ 2");
    }
  }
  .plone-toolbar-left-expanded & {
    @media(min-width: (@plone-screen-sm-min + @plone-left-toolbar-expanded)) {
      margin-left: calc(~"-100vw / 2 +" (@plone-container-sm + @plone-grid-gutter-width + @plone-left-toolbar-expanded) ~"/ 2");
      margin-right: calc(~"-100vw / 2 +" (@plone-container-sm + @plone-grid-gutter-width + @plone-left-toolbar-expanded) ~"/ 2");
    }
    @media(min-width: (@plone-screen-md-min + @plone-left-toolbar-expanded)) {
      margin-left: calc(~"-100vw / 2 +" (@plone-container-md - @plone-grid-gutter-width + @plone-left-toolbar-expanded) ~"/ 2");
      margin-right: calc(~"-100vw / 2 +" (@plone-container-md - @plone-grid-gutter-width + @plone-left-toolbar-expanded) ~"/ 2");
    }
    @media(min-width: (@plone-screen-lg-min + @plone-left-toolbar-expanded)) {
      margin-left: calc(~"-100vw / 2 +" (@plone-container-lg - @plone-grid-gutter-width + @plone-left-toolbar-expanded) ~"/ 2");
      margin-right: calc(~"-100vw / 2 +" (@plone-container-lg - @plone-grid-gutter-width + @plone-left-toolbar-expanded) ~"/ 2");
    }
  }
}

.mosaic-grid-row {
  &.mosaic-row-full-width {
    margin: 0;
    padding-left: 0;
    padding-right: 0;
    max-width: none;
    margin-left: -15px !important;
    margin-right: -15px !important;
    width: ~"calc(100% + 30px)" !important;
    .mosaic-tile-full-width {
      margin-left: 0 !important;
      margin-right: 0 !important;
    }
    .mosaic-width-full .mosaic-tile-full-width {
      margin-left: -15px !important;
      margin-right: -15px !important;
    }
  }
}

.mosaic-row-shrink {
  .mosaic-width-quarter {
      width: 25% !important;
  }
  .mosaic-width-third {
      width: 33% !important;
  }
  .mosaic-width-half {
      width: 50% !important;
  }
  .mosaic-grid-cell {
      margin: 0;
      float: left;
  }
}

.mosaic-row-drop-last {
  @media(max-width: (@plone-screen-sm-min - 1)) {
      .mosaic-width-quarter {
          width: 33% !important;
      }
      .mosaic-width-third {
          width: 50% !important;
      }
      .mosaic-width-half {
          width: 100% !important;
      }
      .mosaic-grid-cell {
          margin: 0;
          float: left;
          &:last-child {
              display: none;
          }
      }
  }
}

